"use client";
import Image from "next/image";
import Link from "next/link";
import { useForm } from 'react-hook-form';
import { User } from "@/type/user";
import { userService } from "@/lib/services/userService";
import { useState } from "react";
export default function Login(){

  const { register, handleSubmit, formState: {errors}} = useForm<User>();
  const [isLoading , setIsLoading] = useState<boolean>(false)
  const onSubmit = (data:User) =>{
    setIsLoading(true)
    userService.login(data).then((res:any)=>{
        localStorage.setItem('access',res.data.data.access)
        localStorage.setItem('refresh',res.data.data.refresh)
        setIsLoading(false)
    }).catch((error:any)=>{
       setIsLoading(false)
    })
  }

    return(
        <div className="w-full h-screen flex items-center justify-center bg-gray-100 dark:bg-gray-900">
        <div className="w-1/2  bg-white flex  justify-between  ">
            <div className="flex-1 flex flex-col items-center justify-center gap-4 mb-8 py-12 px-10">
                
                 <Image
                          className="dark:invert"
                          src="/globe.svg"
                          alt="Next.js logo"
                          width={180}
                          height={38}
                          priority
                        />
                        <div className="flex flex-col items-center">

                        
                  <h3 className="font-montserrat font-medium text-[23px] text-gray-900 text-center dark:text-white my-8 tracking-[12px]">Welcome Back</h3>
                  <span className="w-full text-center mb-8 font-normal text-[14px] text-gray-700 dark:text-gray-300">please enter your credientials to log in</span>
                 <form onSubmit ={handleSubmit(onSubmit)} className="w-full">
                    
                    
                            <input 
                            type="text" 
                             {...register("username", {required:"Username Required"})}
                            placeholder="Username"
                            className="w-full h-10 px-4 border border-gray-300 rounded-lg mb-4 focus:outline-none focus:border-blue-500 "
                            />
                            {errors.username && <p className="text-red-500 text-sm">{errors.username.message}</p>}

                            <input
                            type="password"
                            {...register("password",{required:"Password Required"})}
                            placeholder="Password"
                            className="w-full h-10 px-4 border border-gray-300 rounded-lg mb-2 focus:outline-none focus:border-blue-500"
                            />
                            {errors.password && <p className="text-red-500 text-sm">{errors.password.message}</p>}
                            <Link className="self-start text-[14px] text-gray-700" href='/'>Forgot password?</Link>
                            <button type="submit" className="w-full bg-black text-white text-center py-2 mt-5 font-medium rounded-lg hover:bg-black/80 transition-all duration-300 cursor-pointer" >
                            {isLoading?<span className="inline-block w-6 h-6 border-4 border-blue-500 border-t-transparent rounded-full animate-spin"></span>:"Sign In"}
                          
                            
                            </button>
                  </form>
                    </div>
            </div>
            <div className="flex-1 flex flex-col items-center justify-center  py-12 px-10 bg-black rounded-l-4xl ">
                <div>
                     <Image 
                        className="dark:invert"
                        src="/globe.svg"
                        alt="Vercel logomark"
                        width={180}
                        height={38}
                        
                            />
                    <h3 className="font-montserrat font-medium text-[23px] text-white text-center my-8 tracking-[12px]">BookWarm</h3>
                </div>
              
                            <span className="text-[14px] text-white my-8 font-normal"> New to our platform? Sign Up now.</span>

                            <Link href={'/register'} className="py-2 px-16 text-center border border-white text-white rounded-lg hover:bg-white/80  transition-all duration-300 cursor-pointer">Sign Up</Link>
            </div>
        </div>

        </div>
       
    )
}